.header {
    padding: 20px;
    text-align: center;
    background-color: rgb(243, 243, 243);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}
.header img {
    border-radius: 100px;
}
.name {
    background-color: rgb(243, 243, 243);
}
.info {
    background-color: rgb(243, 243, 243);
    font-style: italic;

}
ul {
    background-color: rgb(243, 243, 243);
}
li {
    background-color: rgb(243, 243, 243);
    font-style: italic;
    color: black;
    padding-right: 10px;
}
li:link {
    color: black;
}
li:hover {
    background-color: white;
    color: black;
}
li:hover:focus {
    background-color: white;

}